import { useState, useEffect } from 'react';
import Draw from './CanvasFunctions';

export default function DrawTool({ canvasRef, setPaths, page, color, width }) {
    const [drawing, setDrawing] = useState(false);

    useEffect(() => {
        const canvas = canvasRef.current;

        function mouseDown(e) {
            setPaths(prevState => {
                const update = (prevState.has(page) ? prevState.get(page).slice() : [])
                update.push({
                    nodes: [Draw.getPos(e, canvas), Draw.getPos(e, canvas)],
                    color,
                    width
                });
                return new Map(prevState.set(page, update))
            })

            setDrawing(true);
        }

        function mouseMove(e) {
            
            if (drawing) {
                const newPos = Draw.getPos(e, canvas);

                setPaths(prevState => {
                    const update = prevState.get(page).slice()
                    const path = update[update.length - 1].nodes.slice()
                    if (Draw.nodeDistance(newPos, path[path.length - 2]) > 7) {
                        path.push(newPos)
                    } else {
                        path[path.length - 1] = newPos
                    }

                    update[update.length - 1].nodes = path

                    return new Map(prevState.set(page, update))
                });
            }
        }

        function mouseUp() {
            setDrawing(false);
        }

        canvas.addEventListener("mousedown", mouseDown);
        canvas.addEventListener("touchstart", mouseDown);
        window.addEventListener("mousemove", mouseMove);
        window.addEventListener("touchmove", mouseMove);
        window.addEventListener("mouseup", mouseUp);
        window.addEventListener("touchend", mouseUp);

        return () => {
            canvas.removeEventListener("mousedown", mouseDown);
            canvas.removeEventListener("touchstart", mouseDown);
            window.removeEventListener("mousemove", mouseMove);
            window.removeEventListener("touchmove", mouseMove);
            window.removeEventListener("mouseup", mouseUp);
            window.removeEventListener("touchend", mouseUp);
        };
    });
}