import { useContext, useEffect, useState } from 'react';
import supabase from 'shared/supabase';
import AspectRatio from '../shared/AspectRatio'
import Search from './Search'
import HideContent from './HideContent';
import Options from '../shared/Options'
import Canvas from 'edit/Canvas';
import DrawRender from 'edit/DrawRender';
import { AuthContext } from '../shared/AuthContext';
import style from 'css/Book.module.css'
import Buttons from './Buttons';
import { PDFContext } from 'shared/PDFContext';

export default function Book() {
    const { page, setPage, useShow, loading } = useContext(PDFContext)
    const { username } = useContext(AuthContext)
    const [hide, setHide] = useState(false)
    const [paths, setPaths] = useState([])

    useShow()

    useEffect(() => {
        setPaths(false)

        if (navigator.onLine) async()

        async function async() {
            const { data, error } = await supabase
                .from('paths')
                .select('path')
                .eq('page', page)
                .eq('to_user', username)

            if (error !== null) return console.log(error?.message)

            let paths = []
            data.forEach(element => paths = paths.concat(element.path))
            setPaths(paths)
        }
    }, [page, username])

    return (
        <>
            <Buttons>
                <Search changePage={setPage} />
                <HideContent hide={hide} setHide={setHide} />
            </Buttons>
            {!loading &&
                <AspectRatio className={style.book}>
                    <Canvas className={hide.toString()}>
                        {canvasRef => (
                            paths && <DrawRender paths={paths} />
                        )}
                    </Canvas>
                </AspectRatio>
            }
            <Options current='book' />
        </>
    )
}