import Draw from './CanvasFunctions';

export default function DrawRender({ paths }) {
    return paths.map((path, index) =>
        <Test
            key={index}
            nodes={path.nodes}
            width={path.width}
            color={path.color}
        />
    )

    function Test({ nodes, width, color }) {
        return (
            <path
                d={ "M" + Draw.posToString(nodes[0]) + nodes.slice(1).map((node) => "L" + Draw.posToString(node)).join(" ") } 
                strokeWidth={width} 
                stroke={color}
            />
        )
    }
}