import { useNavigate } from 'react-router-dom';
import { useContext } from 'react';
import { AuthContext } from '../shared/AuthContext';
import Toggle from './Toggle';
import supabase from '../shared/supabase';
import Options from '../shared/Options';
import Button from 'shared/Button';
import style from 'css/Settings.module.css'

export default function Settings() {
    const navigate = useNavigate();
    const { username, updateUsername } = useContext(AuthContext)

    return (
        <>
            <div className={style.container}>
                <Setting name='username' >
                    <p>
                        Your username is <br />
                        <strong>{username}</strong>
                    </p>
                </Setting>
                <Setting>
                    <Toggle />
                </Setting>
                <Setting>
                    <Button
                        className={style.logout}
                        onClick={() => {
                            supabase.auth.signOut()
                            updateUsername()
                            localStorage.removeItem('to_user')
                            localStorage.removeItem('suggestion')
                            localStorage.removeItem('own_open')
                            navigate('/login')
                        }}
                    >
                        Logout
                    </Button>
                </Setting>
            </div>
            <Options current='settings' />
        </>
    )

    function Setting({ children }) {
        return (
            <div className={style.setting}>
                <div className={style.content}>
                    {children}
                </div>
            </div>
        )
    }
}