import { createContext, useEffect, useState } from 'react'
import PDFViewer from 'book/PDFViewer'

const PDFContext = createContext()

function PDFContextProvider({ children }) {
    const [page, setPage] = useState(parseInt(localStorage.getItem('page') ?? 1))
    const [show, setShow] = useState(false)
    const [disable, setDisable] = useState(true)
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        localStorage.setItem('page', page);
    }, [page])

    function useShow() {
        useEffect(() => {
            setShow(true)
            setDisable(false)

            return () => {
                setShow(false)
                setDisable(true)
            }
        }, [])
    }

    return (
        <PDFContext.Provider value={{
            page,
            setPage,
            show,
            setShow,
            useShow,
            disable,
            setDisable,
            loading,
            setLoading
        }}
        >
            <PDFViewer />
            {children}
        </PDFContext.Provider>
    );
};

export { PDFContext, PDFContextProvider }