import style from 'css/Startpage.module.css'
import { Link } from 'react-router-dom'
import google_logo from 'assets/google_logo.svg'
import apple_logo from 'assets/apple_logo.svg'
import { Globe } from 'react-feather'
import icon from 'assets/icon.svg'

export default function Startpage() {
  const Google = () => (
    <>
      <a
        className={style.Google}
        href='https://play.google.com/store/apps/details?id=io.github.sjungboken'
      >
        <img
          src={google_logo}
          alt='Google Play logo'
        />
        Install on Google Play
      </a>
      <Link
        className={style.other}
        to='/signup'
      >
        Or use in browser
      </Link>
    </>
  )

  const IOS = () => (
    <>
      <Link
        className={style.IOS}
        to='/ios'
      >
        <img
          src={apple_logo}
          alt='Apple logo'
        />
        Install on iOS
      </Link>
      <Link
        className={style.other}
        to='/signup'
      >
        Or use in browser
      </Link>
    </>
  )

  const Web = () => (
    <>
      <Link
        className={style.Web}
        to='/signup'
      >
        <Globe />
        Try web version
      </Link>
      <p className={style.other}>
        Also available on Google Play and iOS
      </p>
    </>
  )

  const install = (() => {
    const ua = navigator.userAgent
    if (/android/i.test(ua)) return <Google />
    if (/iPad|iPhone/i.test(ua)) return <IOS />
    return <Web />
  })()

  return (
    <div className={style.container}>
      <img src={icon} alt='icon' className={style.icon} />
      <h1 className={style.h1}>Sjungboken</h1>
      <ul className={style.ul}>
        <li>Draw in books</li>
        <li>Search for songs</li>
        <li>Same content as physicall version</li>
      </ul>
      {install}
    </div>
  )
}