import style from 'css/EditOptions/Size.module.css'
import { useContext, useState } from 'react'
import ToolContext from 'edit/ToolContext'
import Button from 'shared/Button'
import share from 'css/EditOptions/Options.module.css'

const border = 2
const padding = 10
const max = 19
let value

export default function Size() {
    const [show, setShow] = useState(false)
    const { tool, changeTool } = useContext(ToolContext)

    function move(e) {
        function calc() {
            const x = (e.constructor.name === 'MouseEvent' ? e : e.touches[0]).clientX - padding - border

            if (x <= 0) return 0
            if (x >= (window.innerWidth - (padding + border) * 2)) return max
            return Math.round(x / (window.innerWidth - (padding + border) * 2) * max)
        }

        value = calc() + 1
    }

    function start() {
        window.addEventListener('mouseup', stop)
        window.addEventListener('touchend', stop)
        window.addEventListener('mousemove', move)
        window.addEventListener('touchmove', move)
        setShow(true)
    }

    function stop() {
        window.removeEventListener('mouseup', stop)
        window.removeEventListener('touchend', stop)
        window.removeEventListener('mousemove', move)
        window.removeEventListener('touchmove', move)
        setShow(false)

        if (value === undefined) return
        changeTool(obj => obj.draw.width = value)
        value = undefined
    }

    return (
        <>
            <Button
                className={share.button + ' ' + style[show ? 'hide' : 'show']}
                style={{ '--width': tool.draw.width / (max + 1) }}
                onMouseDown={start}
                onTouchStart={start}
            >
                <div className={style.size} />
            </Button>
            {show &&
                <div
                    className={style.container}
                    style={{
                        '--padding': padding + 'px',
                        '--border': border + 'px'
                    }}
                >
                    <div className={style.triangle} />
                    <div className={style.circle} />
                </div>
            }
        </>
    )
}