import { useRef, useState, useCallback, useEffect } from 'react';
import { Search as MagnifyingGlass, X } from 'react-feather'
import style from 'css/Search.module.css'
import Button from 'shared/Button';
import pages from './pages'

export default function Search({ changePage }) {
    const searchRef = useRef(null);
    const inputRef = useRef(null);

    const [input, setInput] = useState('')
    const [open, setOpen] = useState(false)

    const keydown = useCallback(e => {
        if (e.key !== 'Escape') return
        setOpen(false)
    }, [])

    const mousedown = useCallback(e => {
        if (!searchRef.current.contains(e.target)) setOpen(false)
    }, [])

    useEffect(() => {
        if (open) {
            inputRef.current.focus()
            window.addEventListener('keydown', keydown)
            window.addEventListener('mousedown', mousedown)
        } else {
            window.removeEventListener('keydown', keydown)
            window.removeEventListener('mousedown', mousedown)
            setInput('')
        }
    }, [open, keydown, mousedown])

    function add (input) {
        const int = Number(input);
        if (Number.isInteger(int) && int > 0) {
            changePageAndClose(int)
            return
        }

        if (pages.has(input)) {
            changePageAndClose(pages.get(input))
            return
        }
    }

    function changePageAndClose (value) {
        changePage(value)
        setOpen(false)
    }

    function results (input) {
        if (input.length <= 1) return

        input = input.toLowerCase()

        //bad performance (?)
        const results = new Map([...pages].filter(([key]) => key.toLowerCase().includes(input)))

        if (results.length === 0) return
        
        let jsx = [];
        results.forEach((value, key) => {
            jsx.push(
                <Button
                    className={style.result}
                    key={key}
                    onClick={() => changePageAndClose(value.page)}
                >
                    {key}
                    <span className={style.category}>
                        {value.category}
                    </span>
                </Button>
            )
        })

        return <>{jsx}</>
    }

    return (
        <>
            {!open &&
                <Button
                    title='search'
                    className={style.open}
                    onClick={() => open ? add(input) : setOpen(true)}
                >
                    <MagnifyingGlass/>
                </Button>
            }
            {open && 
                <div className={style.cover}>
                    <div
                        className={style.content}
                        ref={searchRef}
                    >
                        <input
                            type='text'
                            value={input}
                            onChange={e => setInput(e.target.value)}
                            onKeyDown={e => { e.key === 'Enter' && add(e.target.value) }}
                            autoComplete='false'
                            placeholder='Vers eller sida'
                            className={style.input}
                            ref={inputRef}
                        />
                        <Button
                            className={style.close}
                            onClick={() => setOpen(false)}
                        >
                            <X className={style.x} />
                        </Button>
                        {results(input)}
                    </div>
                </div>
            }
        </>
    );
}
