import { useContext, useState, useEffect } from 'react';
import supabase from 'shared/supabase';
import AspectRatio from '../shared/AspectRatio'
import Search from '../book/Search'
import Options from 'shared/Options';
import EditOptions from './Options/Options'
import Canvas from './Canvas';
import { ToolProvider } from './ToolContext';
import { AuthContext } from '../shared/AuthContext';
import Suggestion from './Suggestion';
import DrawRender from './DrawRender';
import DrawTool from './DrawTool';
import Buttons from 'book/Buttons';
import { PDFContext } from 'shared/PDFContext';
import { ConnectionContext } from 'shared/ConnectionContext'

export default function Edit() {
    const { page, setPage, setShow, setDisable } = useContext(PDFContext)
    const { username: from_user } = useContext(AuthContext)
    const connection = useContext(ConnectionContext)

    const [tool, setTool] = useState({
        currentTool: 'none',
        draw: {
            color: localStorage.getItem('color') ?? '#ff0000',
            width: parseInt(localStorage.getItem('width') ?? 10)
        },
    })
    const [paths, setPaths] = useState(new Map())
    const [pathsPrevious, setPathsPrevious] = useState(new Map())
    const [to_user, setToUser] = useState(false)

    useEffect(() => {
        localStorage.setItem('color', tool.draw.color);
        localStorage.setItem('width', tool.draw.width);
    }, [tool])

    function reset() {
        setPaths(new Map())
        setPathsPrevious(new Map())
        localStorage.removeItem('to_user')
        setToUser(false)
        setShow(false)
    }

    /* Sends new paths to database */
    async function send() {
        if (paths.size === 0) {
            reset()
            return
        }

        if (connection()) return

        const rows = []
        paths.forEach((path, page) => {
            rows.push({
                from_user,
                to_user,
                path,
                page,
            })
        })

        const { error: errorInsert } = await supabase
            .from('paths')
            .insert(rows)

        if (errorInsert !== null) return console.log(errorInsert)

        reset()
    }

    useEffect(() => {
        setDisable(tool.currentTool !== 'none')
    }, [tool, setDisable])

    /* Gets previously drawn paths from database */
    useEffect(() => {
        if (to_user && navigator.onLine) async()

        async function async() {
            const { data, error } = await supabase
                .from('paths')
                .select('path')
                .eq('page', page)
                .eq('to_user', to_user)

            if (error !== null) return console.log(error?.message)
            if (data.length < 1) return

            let datajson = []
            data.forEach(element => datajson = datajson.concat(element.path))

            setPathsPrevious(prevState => {
                const copy = new Map(prevState)
                if (copy.has(page)) {
                    datajson.concat(copy.get(page))
                }
                copy.set(page, datajson)
                return copy
            })
        }
    }, [to_user, page])

    /* Combines previously drawn paths with new ones */
    const combinedPaths = (pathsPrevious.get(page) ?? []).concat(paths.get(page) ?? [])

    return (
        to_user ?
            <>
                <Buttons>
                    <Search changePage={setPage} />
                </Buttons>
                <AspectRatio className='Book'>
                    <Canvas>
                        {canvasRef => (
                            <>
                                {combinedPaths.length > 0 && <DrawRender paths={combinedPaths} />}
                                {tool.currentTool === 'draw' &&
                                    <DrawTool
                                        canvasRef={canvasRef}
                                        setPaths={setPaths}
                                        page={page}
                                        color={tool.draw.color}
                                        width={tool.draw.width}
                                    />}
                            </>
                        )}
                    </Canvas>
                </AspectRatio>
                <ToolProvider
                    value={{
                        send,
                        reset,
                        tool,
                        changeTool,
                        undo: () => {
                            setPaths(prevState => {
                                const update = new Map(prevState)
                                if (!update.has(page)) return update

                                const array = update.get(page)
                                if (array.length < 1) {
                                    update.delete(page)
                                    return update
                                }

                                array.pop()
                                update.set(page, array)
                                return update
                            })
                        }
                    }}
                >
                    <EditOptions />
                </ToolProvider>
            </>

            :

            <>
                <Suggestion setToUser={value => { setToUser(value); setShow(true) }} />
                <Options current='edit' />
            </>
    )

    function changeTool(func) {
        setTool(prevState => {
            const update = { ...prevState }
            func(update)
            return update
        })
    }
}