import { useRef } from 'react';

import './css/Canvas.css';

export default function Canvas({ children, ...props }) {
    const canvasRef = useRef(null);

    return (
        <>
            <div id="canvas-container">
                <svg 
                    viewBox='0 0 300 423' 
                    id="canvas"
                    ref={canvasRef}
                    {...props}
                >
                    {children(canvasRef)}
                </svg>
            </div>
        </>
    );
}