import { Link, useNavigate } from 'react-router-dom'
import { useContext, useState } from 'react'
import { AuthContext } from '../shared/AuthContext'
import Field from './Field'
import Submit from './Submit'
import supabase from '../shared/supabase'
import { User, Lock } from 'react-feather'
import style from 'css/Auth.module.css'
import Error from './Error'
import { ConnectionContext } from 'shared/ConnectionContext'

export default function Auth({ titel, link, text }) {
    const navigate = useNavigate();
    const { updateUsername } = useContext(AuthContext)
    const connection = useContext(ConnectionContext)

    const [error, setError] = useState('')
    const [stil, setStil] = useState('')
    const [password, setPassword] = useState('')
    const [fetching, setFetching] = useState(false)

    const StiL = /^[a-zA-Z]{2}[0-9]{4}[a-zA-Z]{2}(-s|)$/
    const valid = {
        stil: StiL.test(stil),
        password: password.length >= 6,
    }

    function getValid() {
        return !Object.values(valid).includes(false)
    }

    function success() {
        localStorage.removeItem('to_user')
        localStorage.removeItem('suggestion')
        localStorage.removeItem('own_open')

        updateUsername()
        navigate('/settings')
    }

    async function signUp() {
        if (!getValid()) return

        const { error: signUpError } = await supabase.auth.signUp({
            email: stil.slice(0, 8) + '-s@student.lu.se',
            password,
        })
        if (signUpError !== null) return setError(signUpError?.message);

        const { error: sessionError } = await supabase.auth.refreshSession()
        if (sessionError !== null) return setError(sessionError?.message);

        success()
    }

    async function signIn() {
        if (!getValid()) return

        const { error } = await supabase.auth.signInWithPassword({
            email: stil.split('@')[0] + '-s@student.lu.se',
            password: password,
        })
        if (error !== null) return setError(error?.message)

        success()
    }

    async function handleSubmit() {
        if (connection()) return
        setFetching(true)
        if (titel === 'Create account') await signUp()
        else if (titel === 'Login') await signIn()
        setFetching(false)
    }

    return (
        <div className={style.Auth}>
            <form method='post' className={style.form}>
                {error && <Error message={error} onClick={() => setError('')} />}
                <h1 className={style.h1}>{titel}</h1>
                <Field
                    label='StiL'
                    icon={<User />}
                    value={stil}
                    setValue={setStil}
                    valid={valid.stil}
                />
                <Field
                    label='Password'
                    icon={<Lock />}
                    type='password'
                    value={password}
                    setValue={setPassword}
                    valid={valid.password}
                />
                <Submit
                    text={'Submit'}
                    onClick={handleSubmit}
                    disabled={!getValid() || fetching}
                />
                <Link
                    className={style.link}
                    to={link}
                >
                    {text}
                </Link>
            </form>
        </div>
    );
}
