import { useEffect, useState, useContext } from 'react';
import supabase from '../shared/supabase';
import style from'css/Toggle.module.css';
import Button from 'shared/Button';
import { AuthContext } from '../shared/AuthContext';
import { ConnectionContext } from 'shared/ConnectionContext'

export default function Toggle() {
  const { username } = useContext(AuthContext)
  const connection = useContext(ConnectionContext)

  const [open, setOpen] = useState();
  const [changing, setChanging] = useState(false);

  useEffect(() => {
    const ownBookState = localStorage.getItem('own_open');
    ownBookState ? setOpen(ownBookState) : getFromDatabase()

    async function getFromDatabase() {
      if (connection()) return

      const { data, error } = await supabase
      .from('users')
      .select('open')
      .eq('username', username)

      if (error !== null) return console.error(error?.message)
      if (data.length < 1) return console.error('no user found')
      
      setOpen(data[0].open ? 'open' : 'closed')
    }
  }, [connection, username]);

  async function toggle() {
    if (connection()) return

    setChanging(true)
    const { error } = await supabase.rpc('toggle_open')
    setChanging(false)

    if (error !== null) return console.log(error?.message)
    
    setOpen(open === 'open' ? 'closed' : 'open')
  }

  useEffect(() => {
    if (open === undefined) return
    localStorage.setItem('own_open', open);
  }, [open])

  return (
    <>
      {`Your book is ${open ?? 'open'}`}
      <Button
        title='Toggle'
        onClick={toggle}
        className={style.toggle + ' ' + style[open ?? 'open']}
        disabled={changing}
      >
        <div className={style.dot} />
      </Button>
    </>
  )
}