import style from 'css/AspectRatio.module.css'

export default function AspectRatio({
        width = 1,
        height = Math.sqrt(2),
        children,
        ...props
    }) {

    return (
        <div className={style.container}>
            <div
                {...props}
                style={{
                    height: `calc(100vw * ${height / width})`,
                    maxWidth: `calc((100vh - 50px) * ${width / height})`
                }}
            >
                {children}
            </div>
        </div>
    )
}