// Returns a positions on the canvas
function getPos(e, target) {
    return e.type === "touchstart" ?
    getOffset({ x: e.touches[0].clientX, y: e.touches[0].clientY }, target) :
    e.type === "touchmove" || e.type === "touchend" ?
    getOffset({ x: e.changedTouches[0].clientX, y: e.changedTouches[0].clientY }, target) :
    getOffset({ x: e.clientX, y: e.clientY}, target);
}

// Returns a position relative to a target
function getOffset(pos, target) {
    let rect = target.getBoundingClientRect();
    return interpolatePos({ x: pos.x - rect.left, y: pos.y - rect.top }, target);
}

// Converts position to a string
function posToString(pos) {
    return pos.x + " " + pos.y;
}

// Converts a global positions to a coordinate on the canvas
function interpolatePos(pos) {
    return {
        x: roundNumber(300 / document.getElementById("canvas").clientWidth * pos.x, 2),
        y: roundNumber(423 / document.getElementById("canvas").clientHeight * pos.y, 2)
    };
}

// Rounds number to decimal scale
function roundNumber(num, scale) {
    if(!("" + num).includes("e")) {
        return +(Math.round(num + "e+" + scale)  + "e-" + scale);
    } else {
        var arr = ("" + num).split("e");
        var sig = ""
        if(+arr[1] + scale > 0) {
            sig = "+";
        }
        return +(Math.round(+arr[0] + "e" + sig + (+arr[1] + scale)) + "e-" + scale);
    }
}

// Returns distance from last placed node
function nodeDistance(thisNode, lastNode) {
    if (lastNode !== null) {
        return Math.sqrt(Math.pow(thisNode.x - lastNode.x, 2) + Math.pow(thisNode.y - lastNode.y, 2));
    }
    return 10;
}

module.exports = {
    getPos,
    getOffset,
    posToString,
    interpolatePos,
    roundNumber,
    nodeDistance
};