import {
  BrowserRouter,
  Routes,
  Route,
  Navigate,
} from 'react-router-dom'

import React, { useContext } from 'react'
import { AuthContext, AuthContextProvider } from "./shared/AuthContext"
import Startpage from './startpage/Startpage'
import Book from './book/Book'
import Edit from './edit/Edit'
import Login from './auth/Login'
import SignUp from './auth/SignUp'
import Settings from './settings/Settings'
import { PDFContextProvider } from 'shared/PDFContext'
import { ConnectionContextProvider } from 'shared/ConnectionContext'
import IOS from 'iOS/IOS'

export default function App() {

  /* Removes PWA prompt */
  window.addEventListener('beforeinstallprompt', (event) => {
    event.preventDefault()
  })

  function Router() {
    const { username } = useContext(AuthContext)

    return (
      <>
        <Routes>
          <Route path='/' element={ghp()} />
          <Route path='/settings' element={auth(<Settings />)} />
          <Route path='/edit' element={auth(<Edit />)} />
          <Route path='/book' element={auth(<Book />)} />
          <Route path='/signup' element={<SignUp />} />
          <Route path='/login' element={<Login />} />
          <Route path='/ios' element={<IOS />} />
        </Routes>
      </>
    )

    function ghp() {
      const path = localStorage.getItem('path');
      if (path) {
        localStorage.removeItem('path');
        return <Navigate replace to={path} />
      } else {
        return <Startpage />
      }
    }

    function auth(element) {
      return username === undefined ? '' : username ? element : <Navigate replace to='/login' />
    }
  }

  return (
    <React.StrictMode>
      <AuthContextProvider>
        <PDFContextProvider>
          <ConnectionContextProvider>
            <BrowserRouter>
              <Router />
            </BrowserRouter>
          </ConnectionContextProvider>
        </PDFContextProvider>
      </AuthContextProvider>
    </React.StrictMode>
  )
}