import { createContext, useState, useRef } from "react";
import style from 'css/Connection.module.css'
import { Wifi, WifiOff } from "react-feather";
import Button from "./Button";

let animating = false
const duration = 1000
const ConnectionContext = createContext()

function ConnectionContextProvider({ children }) {
  const [show, setShow] = useState(false)

  function handle() {
    setShow(true)

    if (!animating) {
      animating = true

      setTimeout(() => {
        setShow(false)
        animating = false
      }, duration)
    }
  }

  function func() {
    if (!navigator.onLine) handle()
    return !navigator.onLine
  }

  return (
    <ConnectionContext.Provider value={useRef(func).current}>
      {show &&
        <Button
          className={style.container + ' ' + style[navigator.onLine ? 'on' : 'off']}
          style={{ '--duration': duration }}
        >
          {navigator.onLine ?
            <>
              You are online
              <Wifi />
            </>
            :
            <>
              You are offline
              <WifiOff />
            </>
          }
        </Button>
      }
      {children}
    </ConnectionContext.Provider>
  )
}

export { ConnectionContext, ConnectionContextProvider }