import style from 'css/EditOptions/Color.module.css'
import { useContext, useState } from 'react'
import ToolContext from 'edit/ToolContext'
import gradient from 'css/EditOptions/Gradient.module.css'
import Button from 'shared/Button'
import share from 'css/EditOptions/Options.module.css'

const border = 0
const padding = 10
const max = 1535
let value

export default function Color() {
    const [show, setShow] = useState(false)
    const { tool, changeTool } = useContext(ToolContext)

    function move(e) {
        function calc() {
            const x = (e.constructor.name === 'MouseEvent' ? e : e.touches[0]).clientX - padding - border

            if (x <= 0) return 0
            if (x >= (window.innerWidth - (padding + border) * 2)) return max
            return Math.round(x / (window.innerWidth - (padding + border) * 2) * max)
        }

        value = calc()
    }

    function start() {
        window.addEventListener('mouseup', stop)
        window.addEventListener('touchend', stop)
        window.addEventListener('mousemove', move)
        window.addEventListener('touchmove', move)
        setShow(true)
    }

    function stop() {
        window.removeEventListener('mouseup', stop)
        window.removeEventListener('touchend', stop)
        window.removeEventListener('mousemove', move)
        window.removeEventListener('touchmove', move)
        setShow(false)

        if (value === undefined) return
        changeTool(obj => obj.draw.color = color(value))
        value = undefined
    }

    return (
        <>
            <Button
                className={share.button + ' ' + style[show ? 'hide' : 'show']}
                style={{ '--color': tool.draw.color }}
                onMouseDown={start}
                onTouchStart={start}
            >
                <div className={style.color} />
            </Button>
            {show &&
                <div
                    className={style.container}
                    style={{
                        '--padding': padding + 'px',
                        '--border': border + 'px'
                    }}
                >
                    <div className={gradient.gradient} />
                </div>
            }
        </>
    )
}

function color(n) {
    const
        inc = x => x,
        dec = x => 255 - x,
        low = () => 0,
        top = () => 255

    const
        rs = [top, dec, low, low, inc, top],
        gs = [inc, top, top, dec, low, low],
        bs = [low, low, inc, top, top, dec]

    const segment = Math.floor(n / 256)
    n = n % 256

    const
        r = rs[segment](n),
        g = gs[segment](n),
        b = bs[segment](n)

    const format = x => (x < 16 ? '0' : '') + (x.toString(16))

    const hex = '#' + format(r) + format(g) + format(b)
    return hex
}