import './css/Field.css'
import { Check } from 'react-feather';

export default function Field({
    type = 'text',
    icon,
    label,
    value,
    setValue,
    valid = false,
    left
}) {
    return (
        <div className='Field'>
            <input
                type={type}
                name={label}
                autoComplete='false'
                spellCheck='false'
                value={value}
                onChange={e => setValue(e.target.value)}
                autoFocus={false}
                placeholder={label}
            />
            {icon}
            {valid && <Check className='check' />}
            {left && <div className='left'>{left}</div>}
        </div>
    );
}