import Confirm from './Confirm'
import Button from 'shared/Button'
import { Edit2 as Edit, Mic, Camera, Send, Trash2 } from 'react-feather'
import style from 'css/EditOptions/Options.module.css'
import Draw from './Draw'
import ToolContext from '../ToolContext'
import { useContext } from 'react'

export default function Default({ changeContent }) {
    const { send, reset, changeTool } = useContext(ToolContext);

    return (
        <>
            <Button
                className={style.button}
                onClick={() => {
                    changeTool(obj => obj.currentTool = 'draw')
                    changeContent(<Draw />);
                }}
            >
                <Edit />
            </Button>
            <Button
                className={style.button + ' ' + style.disabled}
            >
                <Mic />
            </Button>
            <Button
                className={style.button + ' ' + style.disabled}
            >
                <Camera />
            </Button>
            <Button
                className={style.button}
                onClick={() => {
                    changeContent(<Confirm text='Discard?' confirm={reset} />)
                }}
            >
                <Trash2 />
            </Button>
            <Button
                className={style.button}
                onClick={() => {
                    changeContent(<Confirm text='Send?' confirm={send} />)
                }}
            >
                <Send />
            </Button>
        </>
    )
}