import Button from 'shared/Button'

import { X, Check } from 'react-feather'

import styleShared from 'css/EditOptions/Options.module.css'
import style from 'css/EditOptions/Confirm.module.css'

export default function Confirm({ text, confirm, changeContent }) {

    return (
        <>
            <Button 
                className={styleShared.button}
                onClick={() => {
                    changeContent(false)
                }}
            >
                <X />
            </Button>
            <p className={style.text}>{text}</p>
            <Button 
                className={styleShared.button}
                onClick={() => { 
                    confirm()
                    changeContent(false)
                }}
            >
                <Check />
            </Button>
        </>
    )
}