import { Book, Edit, Settings } from 'react-feather'
import style from 'css/Options.module.css'
import { Link } from 'react-router-dom'

export default function Options ({ current }) {
    return (
        <div className={style.container}>
            <Option
                icon={<Book/>}
                page='book'
            />
            <Option
                icon={<Edit/>}
                page='edit'
            />
            <Option
                icon={<Settings/>}
                page='settings'
            />
        </div>
    );

    function Option({ icon, page }) {
        return (
            <Link
                className={style.option + (page === current ? ' ' + style.current : '')}
                to={'/' + page}
            >
                {icon}
            </Link>
        );
    }
}