import { Eye, EyeOff } from 'react-feather'
import style from 'css/HideContent.module.css'
import Button from 'shared/Button';

export default function HideContent({ hide, setHide }) {
    return (
        <Button
            title='hide'
            className={style.HideContent}
            onClick={() => setHide(!hide)}
        >
            {hide ? <Eye/> : <EyeOff/>}
        </Button>
    );
}
