import { useState, useEffect, useContext } from 'react'
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack'
import 'react-pdf/dist/esm/Page/TextLayer.css'
import 'react-pdf/dist/esm/Page/AnnotationLayer.css'
import { PDFContext } from 'shared/PDFContext'
import style from 'css/PDFViewer.module.css'
import { pdfjs } from 'react-pdf';
import file from 'assets/pdf.pdf'
import { Loader } from 'react-feather'

pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

let pages;
let startX;

export default function PDFViewer() {
    const { page, setPage, show, disable, loading, setLoading } = useContext(PDFContext)

    const [renderPage, setRenderPage] = useState(1)
    const [dimensions, setDimensions] = useState(getDimensions())

    function getDimensions() {
        const menuHeight = 50;

        const maxWidth = window.innerWidth
        const maxHeight = window.innerHeight - menuHeight

        const currentRatio = maxWidth / maxHeight;
        const desiredRatio = 1 / Math.sqrt(2)
        const limit = currentRatio > desiredRatio

        const width = limit ? undefined : maxWidth
        const height = limit ? maxHeight : undefined;

        return { width, height }
    }

    useEffect(() => {
        function resize() { setDimensions(getDimensions()) }
        window.addEventListener('resize', resize);
        return () => window.removeEventListener('resize', resize);
    }, []);

    function Turn() {
        useEffect(() => {
            function changePage(func) {
                if (pages === undefined) return
                setPage(prev => {
                    const current = prev
                    prev = func(prev)
                    return (prev < 1 || prev > pages) ? current : prev
                })
            };

            function start(e) {
                startX = (e.constructor.name === 'MouseEvent' ? e : e.touches[0]).clientX
            }

            function stop(e) {
                const diff = startX - (e.constructor.name === 'MouseEvent' ? e : e.changedTouches[0]).clientX
                if (Math.abs(diff) < 75) return
                changePage(x => x + (diff > 0 ? 1 : - 1))
            }

            function key(event) {
                if (event.key === 'ArrowLeft') changePage(x => x - 1)
                if (event.key === 'ArrowRight') changePage(x => x + 1)
            }

            window.addEventListener('mousedown', start)
            window.addEventListener('touchstart', start)
            window.addEventListener('mouseup', stop)
            window.addEventListener('touchend', stop)
            window.addEventListener('keydown', key);

            return () => {
                window.removeEventListener('mousedown', start)
                window.removeEventListener('touchstart', start)
                window.removeEventListener('mouseup', stop)
                window.removeEventListener('touchend', stop)
                window.removeEventListener('keydown', key);
            }
        }, [])

        return <></>
    }

    return (
        <>
            {(loading && show) && <Loader className={style.loader} />}
            <Document
                file={file}
                className={`${style.document} ${style[loading ? 'loading' : 'done']} ${style[dimensions?.width ? 'vertically' : 'horizontally']} ${style[show ? '' : 'hide']}`}
                onLoadSuccess={pdf => {
                    pages = pdf._pdfInfo.numPages
                }}
            >
                {renderPage !== page &&
                    <Page
                        key={renderPage}
                        pageNumber={renderPage}
                        width={dimensions?.width}
                        height={dimensions?.height}
                    />
                }
                <Page
                    key={page}
                    pageNumber={page}
                    onRenderSuccess={() => {
                        setRenderPage(page)
                        setLoading(false)
                    }}
                    width={dimensions?.width}
                    height={dimensions?.height}
                    className={style[renderPage === page ? 'show' : 'hide']}
                />
            </Document>
            {(show && !disable) && <Turn />}
        </>
    )
}