import { useContext } from 'react'
import Button from 'shared/Button'
import ToolContext from 'edit/ToolContext'
import { ArrowLeft, RotateCcw } from 'react-feather'
import styleShared from 'css/EditOptions/Options.module.css'
import Color from './Color'
import Size from './Size'

export default function Draw({ changeContent }) {
    const { changeTool, undo } = useContext(ToolContext)

    return (
        <>
            <Button
                className={styleShared.button}
                onClick={() => {
                    changeTool(obj => obj.currentTool = 'none')
                    changeContent(false)
                }}
            >
                <ArrowLeft />
            </Button>
            <Color />
            <Size />
            <Button
                className={styleShared.button}
                onClick={undo}
            >
                <RotateCcw alt="undo" />
            </Button>
        </>
    )
}