import Button from 'shared/Button';
import style from 'css/Suggestion.module.css'
import { RotateCw, Check } from 'react-feather';
import { useContext, useState, useCallback, useEffect } from 'react';
import supabase from 'shared/supabase';
import { ConnectionContext } from 'shared/ConnectionContext'

export default function Suggestion({ setToUser }) {
    const connection = useContext(ConnectionContext)

    const [user, setUser] = useState()
    const [changing, setChanging] = useState(false)

    const set_random_username = useCallback(async (previous = '') => {
        const { data, error } = await supabase
            .rpc('get_random_username', { previous })

        if (error !== null) return console.log(error?.message)

        localStorage.setItem('suggestion', data)
        if (data.length > 0) setUser(data[0])
    }, [])

    useEffect(() => {
        const to_user = localStorage.getItem('to_user')
        if (to_user) return setToUser(to_user)

        const suggestion = localStorage.getItem('suggestion')
        if (suggestion) return setUser(suggestion)

        if (connection()) return
        set_random_username()
    }, [setToUser, connection, set_random_username])

    async function newSuggestion() {
        if (connection()) return
        setChanging(true)
        await set_random_username(user)
        setChanging(false)
    }

    return (
        user &&
        <div className={style.suggestion}>
            <p className={style.text}>
                Edit the book of
                <br />
                <span className={style.name}>{user}</span>
            </p>
            <div className={style.buttons}>
                <Button
                    title='next'
                    className={style.button}
                    onClick={newSuggestion}
                    disabled={changing}
                >
                    <RotateCw />
                </Button>
                <Button
                    title='ok'
                    className={style.button}
                    onClick={async () => {
                        setToUser(user)
                        if (navigator.onLine) set_random_username(user)
                        localStorage.setItem('to_user', user)
                    }}
                >
                    <Check />
                </Button>
            </div>
        </div>
    )
}